import React from "react";
import { Container, Row, Col, Card, CardBody } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import SlideToggle from "react-slide-toggle";
import "../assets/styles.scss";
import Modal from "../components/components-overview/Modal"



{/*id : 1,
    nama : "Bambang",
    alamat : "Surabaya",
    produk : [{id:1, jumlah : 5}],
    totalHarga : "Rp 55.000,-" */}



const renderTableItem = (_Item) => {
    return (
        <tr>
            <td>{_Item.id}</td>
            <td>{_Item.nama}</td>
            <td>{_Item.diskon}</td>
            <td>{_Item.jenis}</td>
            <td>{_Item.stok}</td>
            <td>{_Item.Expired}</td>
            <td> <span className={"_" + _Item.status}>
                {(() => {
                    switch (_Item.status) {
                        case "Aktif": return "Aktif";
                        case "Kadaluarsa": return "Kadaluarsa";
                        case "Habis": return "Habis";
                        default: return "";
                    }
                })()}
            </span>
            </td>
            <td> <i class="large material-icons">create</i> </td>

        </tr>
    );
}





const renderTables = (_Category = "Tabel Pesanan") => {
    var tableRender = [];
    return (
        <Row>
            <Col>
                <button className="_button-toggle _active">Semua</button>
                <button className="_button-toggle">Aktif</button>
                <button className="_button-toggle">Habis</button>
                <button className="_button-toggle">Kadaluarsa</button>
                <Card small className="mb-4">
                    <CardBody className="p-0 pb-3 table-responsive-lg">
                        <table className="table mb-0">
                            <thead className="thead-background-color">
                                <tr>
                                    <th scope="col" className="border-0">Id</th>
                                    <th scope="col" className="border-0">Pemesan</th>
                                    <th scope="col" className="border-0">Diskon</th>
                                    <th scope="col" className="border-0">Jenis</th>
                                    <th scope="col" className="border-0">Stok</th>
                                    <th scope="col" className="border-0">Expired</th>
                                    <th scope="col" className="border-0">Status</th>
                                    <th scope="col" className="border-0"></th>
                                </tr>
                            </thead>
                            <tbody>
                                {orderData.map((_data, _index) => {
                                    return (
                                        <SlideToggle
                                            duration={0}
                                            collapsed={true}
                                            render={({ onToggle }) => (
                                                <div hidden>
                                                    {tableRender.push(renderTableItem(_data, onToggle))}
                                                </div>
                                            )} />
                                    )
                                })}
                                {tableRender}
                            </tbody>
                        </table>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
}

