class VoucherManagement extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isShowing: false,
            status: "",
        }
    }
    openModalHandler = () => {
        this.setState({
            isShowing: true,
            status: "voucher"
        });
    }

    closeModalHandler = () => {
        this.setState({
            isShowing: false
        });
    }

    render() {
        return (
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Manajemen Voucher" subtitle="" className="text-sm-left" />
                    <div className="col">
                        <button className="button-addDriver" onClick={this.openModalHandler}> <i className="large material-icons">add</i>Tambah Voucher</button>
                        <Modal
                            className="_modal"
                            show={this.state.isShowing}
                            close={this.closeModalHandler}
                            status={this.state.status}>
                        </Modal>
                    </div>
                      </Row>
                {/* Default Light Table */}
                {renderTables()}
            </Container>
        )
    }
}



export default VoucherManagement;
