import React from "react";
import { Container, Row, Col, Card, CardBody } from "shards-react";

import PageTitle from "../components/common/PageTitle";
import SlideToggle from "react-slide-toggle";
import "../assets/styles.scss";
import Modal from "../components/components-overview/Modal"


const renderTableItem = (_Item, onToggle) => {
    return (
        <tr>
            <td> <input type="checkbox" id={_Item.id} />
                <label for={_Item.id}></label>
            </td>
            <td>{_Item.id}</td>
            <td>{_Item.jenis}</td>
            <td>{_Item.pemesanan}</td>
            <td>{_Item.telepon}</td>
            <td>{_Item.alamatPenjemput}</td>
            <td>{_Item.alamatTujuan}</td>
            <td>{_Item.harga}</td>
            <td><Row>
                <Col lg="12">
                    <button className="_btn button-setDriver" >
                        Set Driver
                    </button>
                </Col>
            </Row>
            </td>
        </tr>
    );
}


const renderTables = (_Category = "Tabel Pesanan") => {
    var tableRender = [];
    return (
        <Row>
            <Col>
                <Card small className="mb-4">
                    {/* <CardHeader className="border-bottom">
                    <h6 className="m-0">{_Category}</h6>
                </CardHeader> */}
                    <CardBody className="p-0 pb-3 table-responsive-lg">
                        <table className="table mb-0">
                            <thead className="thead-background-color">
                                <tr>
                                    <th width="5px" scope="col" className="border-0">
                                        <input type="checkbox" id="selectall" />
                                        <label for="selectall"></label>
                                    </th>
                                    <th scope="col" className="border-0">Id</th>
                                    <th scope="col" className="border-0">Jenis</th>
                                    <th scope="col" className="border-0">Pemesan</th>
                                    <th scope="col" className="border-0">Telepon</th>
                                    <th scope="col" className="border-0">Alamat Penjemputan</th>
                                    <th scope="col" className="border-0">Alamat Tujuan</th>
                                    <th scope="col" className="border-0">Harga</th>
                                    <th scope="col" className="border-0">Driver</th>
                                </tr>
                            </thead>
                            <tbody>
                                {orderData.map((_data, _index) => {
                                    return (
                                        <SlideToggle
                                            duration={0}
                                            collapsed={true}
                                            render={({ onToggle }) => (
                                                <div hidden>
                                                    {tableRender.push(renderTableItem(_data, onToggle))}
                                                </div>
                                            )} />
                                    )
                                })}
                                {tableRender}
                            </tbody>
                        </table>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
}

const orderData = [{
    id: 1,
    jenis: "Anut - Motor",
    pemesanan: "Agus Sudrajat",
    telepon: "081123456789",
    alamatPenjemput: "Jl. Ahmad Yani 14",
    alamatTujuan: "Jl. Menur Pumpungan No 15",
    harga: "Rp. 15.000"
}, {
    id: 2,
    jenis: "Lekas",
    pemesanan: "Dian Kusumawati",
    telepon: "081234567890",
    alamatPenjemput: "Jl. Yos Sudarso  14",
    alamatTujuan: "Jl. Mawar 38",
    harga: "Rp. 7.000"
}
]



class NewOrder extends React.Component {

    constructor() {
        super();

        this.state = {
            isShowing: false
        }
    }

    openModalHandler = () => {
        this.setState({
            isShowing: true,
            status : "cancel"
        });
    }

    closeModalHandler = () => {
        this.setState({
            isShowing: false
        });
    }


    render() {
        return (
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Pesanan Baru" subtitle="" className="text-sm-left" />

                    {/* <button className="button-cancel">Cancel</button> */}
                    {this.state.isShowing ? <div onClick={this.closeModalHandler} className="back-drop"></div> : null}
                    <div className="col">
                        <button className="button-cancel" onClick={this.openModalHandler}>Cancel</button>
                    </div>
                    <Modal
                        className="_modal"
                        show={this.state.isShowing}
                        close={this.closeModalHandler}
                        status={this.state.status}>
                    </Modal>
                </Row>
                {/* Default Light Table */}
                {renderTables()}
            </Container>
        )
    }
}



export default NewOrder;
