import React from "react";
import { Container, Row, Col, Card, CardBody } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import SlideToggle from "react-slide-toggle";
import "../assets/styles.scss";
import Modal from "../components/components-overview/Modal"

class SanguManagement extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            Showing: false,
            status:"",
            itemId : 0
        }
    }

    openModalLihatBuktiTransfer = (item) =>{
        this.setState({
            Showing:true,
            status : "buktiTransfer",
            Pemesan: item.pemesanan,
            Harga: item.harga
        })
    }
    closeModal= () =>{
        this.setState({
            Showing:false,
        })
    }

    openModalIsiSangu= (item) =>{
        this.setState({
            Showing:true,
            status :"isiSangu",
            Pemesan: item.pemesanan,
            Harga: item.harga
        })
    }
    
    render() {
        const renderTableItem = (_Item) => {
            return (
                <tr>
                    <td>{_Item.id}</td>
                    <td>{_Item.pemesanan}</td>
                    <td>{_Item.harga}</td>
                    <td><button onClick={() =>this.openModalLihatBuktiTransfer(_Item)} className="_button_warning">Lihat</button></td>
                    <td><button onClick={() =>this.openModalIsiSangu(_Item)} className="_button_warning">Isi Sangu</button></td>
                </tr>
            );
        }

        const renderTables = (_Category = "Tabel Pesanan") => {
            var tableRender = [];
            return (
                <Row>
                    <Col>
                    <button className="_button-toggle _active">Transaksi Baru</button>
                    <button className="_button-toggle">History</button>
                        <Card small className="mb-4">
                            <CardBody className="p-0 pb-3 table-responsive-lg">
                                <table className="table mb-0">
                                    <thead className="thead-background-color">
                                        <tr>
                                            <th scope="col" className="border-0">Id</th>
                                            <th scope="col" className="border-0">Nama User</th>
                                            <th scope="col" className="border-0">Nominal</th>
                                            <th scope="col" className="border-0">Bukti Transfer</th>
                                            <th scope="col" className="border-0"></th>  
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {orderData.map((_data, _index) => {
                                            return (
                                                <SlideToggle
                                                    duration={0}
                                                    collapsed={true}
                                                    render={({ onToggle }) => (
                                                        <div hidden>
                                                            {tableRender.push(renderTableItem(_data, onToggle))}
                                                        </div>
                                                    )} />
                                            )
                                        })}
                                        {tableRender}
                                    </tbody>
                                </table>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            );
        }
        
        const orderData = [{
            id: 1,
            pemesanan: "Agus Sudrajat",
            harga: "Rp. 70.000"
        }, {
            id: 2,
            pemesanan: "Dian Kusumawati",
            harga: "Rp. 100.000"
        },
        {
            id: 3,
            pemesanan: "Lusiana Dewi F.",
            harga: "Rp. 17.000"
        },
        {
            id: 4,
            pemesanan: "Totok Budiarto",
            harga: "Rp. 4.000" 
        }]
        
        console.log(this.state)
        return (
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Manajemen Sangu" subtitle="" className="text-sm-left" />
                    <div className="col">
                    <Modal
                        className="_modal"
                        show={this.state.Showing}
                        close={this.closeModal}
                        status={this.state.status}
                        _dataPemesanan= {this.state.Pemesan}
                        _dataHarga={this.state.Harga}
                        >
                    </Modal>
                    </div>
                </Row>
                {/* Default Light Table */}
                {renderTables()}
            </Container>
        )
    }
}



export default SanguManagement;
