import React from 'react';

import '../../assets/_modal.css';

const modal = (props) => {
    console.log(props)
    if (props.status === "cancel") {
        return (
            <div className="_modal-wrapper"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                </div>
                <div className="_modal-body">
                    <p>
                        {/* {props.children} */}
                        <text className="_textMessage">   Apakah anda yakin membatalkan pesanan ini ?
                        <text className="_textMessage2">(action tidak dapat dibatalkan)</text></text>
                    </p>

                </div>
                <div className="_modal-footer">
                    <button className="_btn btn-batalkanPesanan" onClick={props.close}>Batalkan Pesanan</button>
                    <button className="_btn btn-kembali">Kembali</button>
                </div>
            </div>
        )
    } else if (props.status === "selesai") {
        return (
            <div className="_modal-wrapper"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                </div>
                <div className="_modal-body">
                    <p>
                        {/* {props.children} */}
                        <text className="_textMessage">Apakah anda yakin Menandai pesanan ini selesai ?
                        <text className="_textMessage2">(action tidak dapat dibatalkan)</text></text>
                    </p>

                </div>
                <div className="_modal-footer">
                    <button className="_btn btn-selesai">Pesanan Selesai</button>
                    <button className="_btn btn-kembali" onClick={props.close}>Kembali</button>
                </div>
            </div>
        )
    } else if (props.status === "addDriver") {
        return (
            <div className="_modal-wrapper2"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                    {/* <center>Tambah Voucher</center> */}
                    <h5 class="_modal-title modal-title text-center">Tambah Driver</h5>
                </div>
                <div className="_modal-body">
                    <input className="_inputStyle2" placeholder="Nama" type="text" />
                    <input className="_inputStyle2" placeholder="Email" type="text" />
                    <input className="_inputStyle2" placeholder="Password" type="text" />
                    <input className="_inputStyle2" placeholder="Confirm Password" type="text" />
                    <input className="_inputStyle2" placeholder="Phone Number" type="text" />

                    <div class="form-group">
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio1" />
                            <label className="_text-primary" for="Radio1">Motor</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio2" />
                            <label className="_text-primary" for="Radio2">Mobil</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio3" />
                            <label className="_text-primary" for="Radio3">Perempuan</label>
                        </div>
                    </div>
                    <input className="_inputStyle2" placeholder="Alamat" type="" />
                    <input className="_inputStyle2" placeholder="Foto Driver" type="text" />
                    <input className="_inputStyle2" placeholder="Foto Ktp Driver" type="text" />
                </div>
                <div className="_modal-footer">
                    <center>
                        <button className="_btn _button_warning">Tambah</button>
                    </center>
                </div>
            </div>
        )
    } else if (props.status === "voucher") {
        return (
            <div className="_modal-wrapper2"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                    {/* <center>Tambah Voucher</center> */}
                    <h5 class="_modal-title modal-title text-center">Tambah Voucher</h5>
                </div>
                <div className="_modal-body">
                    <input className="_inputStyle2" placeholder="Nama Voucher" type="text" />
                    <div class="form-group">
                        <p className="text-left _text_radio">Tipe Diskon</p>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio1" />
                            <label className="_text-primary" for="Radio1">Persentase</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio2" />
                            <label className="_text-primary" for="Radio2">Potongan</label>

                        </div>
                    </div>
                    <input className="_inputStyle2" placeholder="Banyak Diskon" type="" />
                    <div class="form-group">
                        <p className="text-left _text_radio">Tipe Layanan</p>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio12" />
                            <label className="_text-primary" for="Radio12">Motor</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio22" />
                            <label className="_text-primary" for="Radio22">Mobil</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio23" />
                            <label className="_text-primary" for="Radio23">Lekas</label>
                        </div>
                        <div className="form-check-inline _radio">
                            <input type="checkbox" className="" id="Radio24" />
                            <label className="_text-primary" for="Radio24">Basah</label>
                        </div>
                    </div>
                    <input className="_inputStyle2" placeholder="Banyak Voucher" type="text" />
                    <input className="_inputStyle2" placeholder="Tanggal Kadaluarsa" type="text" />
                </div>
                <div className="_modal-footer">
                    <center>
                        <button className="_btn _button_warning">Tambah</button>
                    </center>
                </div>
            </div>
        )
    } else if (props.status === "buktiTransfer") {
        return (
            <div className="_modal-wrapper2"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                    {/* <center>Tambah Voucher</center> */}
                    <h5 class="_modal-title modal-title text-center">Bukti Transfer</h5>
                </div>
                <div style={{ padding: "5%" }} className="_modal-body text-left">
                    <br />
                    <dl class="row">
                        <dt class="col-sm-5">Bukti Transfer</dt>
                        <dd class="col-sm-5">
                            <dl class="row">
                                <dt class="col-sm-12"> : {props._dataPemesanan}</dt>
                            </dl>
                        </dd>
                        <dt class="col-sm-5">Nominal Top Up</dt>
                        <dd class="col-sm-5">
                            <dl class="row">
                                <dt class="col-sm-12"> : {props._dataHarga}</dt>
                            </dl>
                        </dd>
                        <label style={{ float: "left" }}>Bukti Transfer</label>
                        <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSDnlNcfAZ9XMm8C0JNRd1tj0Eqph7KLQyWc3eUPKmfdPx1L7d6" alt="..." className="img-thumbnail" />
                    </dl>
                </div>
            </div>
        )
    } else if (props.status === "isiSangu") {
        return (
            <div className="_modal-wrapper2"
                style={{
                    transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                    opacity: props.show ? '1' : '0'
                }}>
                <div className="_modal-header">
                    <button className="close-modal-btn" onClick={props.close}><i class="large material-icons">close</i></button>
                    {/* <center>Tambah Voucher</center> */}
                    <h5 class="_modal-title modal-title text-center">Isi Sangu</h5>
                </div>
                <div className="_modal-body">
                    {/* {props.children} */}
                    <text className="_textMessage">Apakah anda yakin Mengisi sangu {props._dataPemesanan}
                        <text className="_textMessage2">senilai {props._dataHarga}?</text>
                        <text className="_textMessage2">(Pastikan sudah melihat bukti transfer yang dikirim oleh user)</text>
                        <text className="_textMessage2">(Action ini tidak dapat dibatalkan)</text>
                    </text>
                </div>
                <div className="_modal-footer">
                    <button className="_btn btn-selesai">Isi Sangu</button>
                    <button className="_btn btn-kembali" onClick={props.close}>Kembali</button>
                </div>
            </div>
        )
    } else {
        return (
            <div>

            </div>
        )

    }
}

export default modal;