class OnProcessOrder extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            isShowing: false,
            status : "",
        }
    }

    openModalHandlerCancel = () => {
        this.setState({
            isShowing: true,
            status: "cancel"
        });
    }

    closeModalHandlerCancel = () => {
        this.setState({
            isShowing: false
        });
    }
    openModalHandlerSelesai = () => {
        this.setState({
            isShowing: true,
            status : "selesai"
        });
    }

    closeModalHandlerSelesai = () => {
        this.setState({
            isShowing: false
        });
    }

    render() {
        return (
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Pesanan Diproses" subtitle="" className="text-sm-left" />
                    <div className="col">
                    </div>
                    <div className="col">
                        <button className="button-selesai" onClick={this.openModalHandlerSelesai} >Selesai</button>
                    </div>
                    <div className="col">
                        <button className="button-cancel" onClick={this.openModalHandlerCancel}>Cancel</button>
                    </div>
                    <Modal
                        className="_modal"
                        show={this.state.isShowing}
                        close={this.closeModalHandlerCancel}
                        status={this.state.status}>
                    </Modal>
                </Row>
              
                {/* Default Light Table */}
                {renderTables()}
            </Container>
        )
    }
}

export default OnProcessOrder;
