import React from "react";
import { Container, Row, Col, Card, CardBody } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import SlideToggle from "react-slide-toggle";
import "../assets/styles.scss";


{/*id : 1,
    nama : "Bambang",
    alamat : "Surabaya",
    produk : [{id:1, jumlah : 5}],
    totalHarga : "Rp 55.000,-" */}



const renderTableItem = (_Item) => {
    return (
        <tr>
            <td>
                <input type="checkbox" id={_Item.id} />
                <label for={_Item.id}></label>
            </td>
            <td>{_Item.id}</td>
            <td>{_Item.jenis}</td>
            <td>{_Item.pemesanan}</td>
            <td>{_Item.telepon}</td>
            <td>{_Item.alamatPenjemput}</td>
            <td>{_Item.alamatTujuan}</td>
            <td>{_Item.harga}</td>
            <td>{_Item.driver}</td>
        </tr>
    );
}





const renderTables = (_Category = "Tabel Pesanan") => {
    var tableRender = [];
    return (
        <Row>
            <Col>
                <button className="_button-toggle _active">Hari Ini</button>
                <button className="_button-toggle">Bulan Ini</button>
                <button className="_button-toggle">Semua</button>
                <Card small className="mb-4">
                    <CardBody className="p-0 pb-3 table-responsive-lg">
                        <table className="table mb-0">
                            <thead className="thead-background-color">
                                <tr>
                                    <th scope="col" className="border-0">
                                        <input type="checkbox" id="selectall" />
                                        <label for="selectall"></label>
                                    </th>
                                    <th scope="col" className="border-0">Id</th>
                                    <th scope="col" className="border-0">Pemesan</th>
                                    <th scope="col" className="border-0">Telepon</th>
                                    <th scope="col" className="border-0">Alamat Penjemputan</th>
                                    <th scope="col" className="border-0">Alamat Tujuan</th>
                                    <th scope="col" className="border-0">Jarak</th>
                                    <th scope="col" className="border-0">Harga</th>
                                    <th scope="col" className="border-0">Driver</th>
                                </tr>
                            </thead>
                            <tbody>
                                {orderData.map((_data, _index) => {
                                    return (
                                        <SlideToggle
                                            duration={0}
                                            collapsed={true}
                                            render={({ onToggle }) => (
                                                <div hidden>
                                                    {tableRender.push(renderTableItem(_data, onToggle))}
                                                </div>
                                            )} />
                                    )
                                })}
                                {tableRender}
                            </tbody>
                        </table>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
}

