import React from "react";
import {
  Form,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  FormInput
} from "shards-react";

export default () => (
  <Form className="main-navbar__search w-100 d-none d-md-flex d-lg-flex">
    <InputGroup seamless className="ml-3">
      <InputGroupAddon type="prepend">
        <InputGroupText>
          <i style={{fontSize: "20px", color: "black"}} className="large material-icons">search</i>
        </InputGroupText>
        <FormInput style={{fontSize: "14px", color: "black"}}
        className="navbar-search"
        placeholder="Search"
      />
      </InputGroupAddon>
    </InputGroup>
  </Form>
);
