import React from "react";
import { Redirect } from "react-router-dom";

// Layout Types
import { DefaultLayout } from "./layouts";

// Route Views
import BlogOverview from "./views/BlogOverview";
import UserProfileLite from "./views/UserProfileLite";
import AddNewPost from "./views/AddNewPost";
import Errors from "./views/Errors";
import ComponentsOverview from "./views/ComponentsOverview";
import Tables from "./views/Tables";
import BlogPosts from "./views/BlogPosts";
import DriverManagements from "./views/DriverManagement";
import InDevelopment from "./views/InDevelopment";
import NewOrder from "./views/NewOrder";
import AcceptedOrder from "./views/AcceptedOrder";
import RejectedOrder from "./views/RejectedOrder";
import OnProcessOrder from "./views/OnProcessOrder";
import FinishedOrder from "./views/FinishedOrder";
import OnDeliveryOrder from "./views/OnDeliveryOrder";
import LoginForm from "./views/LoginForm";
import VoucherManagement from "./views/VoucherManagement";
import SanguManagement from "./views/SanguManagement";

export default [
  {
    path: "/",
    exact: true,
    layout: DefaultLayout,
    component: () => <Redirect to="/blog-overview" />
  },
  {
    path: "/blog-overview",
    layout: DefaultLayout,
    component: InDevelopment
  },
  {
    path: "/user-profile-lite",
    layout: DefaultLayout,
    component: UserProfileLite
  },
  {
    path: "/add-new-post",
    layout: DefaultLayout,
    component: AddNewPost
  },
  {
    path: "/errors",
    layout: DefaultLayout,
    component: Errors
  },
  {
    path: "/components-overview",
    layout: DefaultLayout,
    component: ComponentsOverview
  },
  {
    path: "/tables",
    layout: DefaultLayout,
    component: Tables
  },
  {
    path: "/blog-posts",
    layout: DefaultLayout,
    component: BlogPosts
  },
  {
    path: "/driver-management",
    layout: DefaultLayout,
    component : DriverManagements
  },
  {
    path: "/voucher-management",
    layout: DefaultLayout,
    component : VoucherManagement
  },
  {
    path: "/sangu-management",
    layout: DefaultLayout,
    component : SanguManagement
  },
  {
    path: "/new-order",
    layout: DefaultLayout,
    component : NewOrder
  },
  {
    path: "/accepted-order",
    layout: DefaultLayout,
    component : AcceptedOrder
  },
  {
    path: "/rejected-order",
    layout : DefaultLayout,
    component : RejectedOrder
  },
  {
    path: "/onprocess-order",
    layout : DefaultLayout,
    component : OnProcessOrder
  },
  {
    path : "/finished-order",
    layout : DefaultLayout,
    component : FinishedOrder
  },
  {
    path : "/ondelivery-order",
    layout : DefaultLayout,
    component : OnDeliveryOrder
  },
  {
    path : "/chat",
    layout : DefaultLayout,
    component : InDevelopment
  },
  {
    path : "/login",
    layout: LoginForm
  }
];
