const renderTableItem = (_Item) => {
    return (
        <tr>
            <td>
                <input type="checkbox" id={_Item.id} />
                <label for={_Item.id}></label>
            </td>
            <td>{_Item.id}</td>
            <td>{_Item.nama}</td>
            <td>{_Item.telepon}</td>
            <td>{_Item.tipeDriver}</td>
            <td>{_Item.alamat}</td>
            <td> <span className={"_" + _Item.status}>
                {(() => {
                    switch (_Item.status) {
                        case "Antri": return "Antri";
                        case "nonAktif": return "Non-Aktif";
                        case "Proses": return "Proses";
                        default: return "";
                    }
                })()}
            </span>
            </td>
            <td> <i class="large material-icons">create</i> </td>

        </tr>);
}

const renderTableItem2 = (_Item) => {
    return (
        <tr>
            <td>
                <input type="checkbox" id={_Item.id} />
                <label for={_Item.id}></label>
            </td>
            <td>{_Item.id}</td>
            <td>{_Item.nama}</td>
            <td>{_Item.telepon}</td>
            <td>{_Item.alamat}</td>
        </tr>);
}






class DriverManagements extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            showDaftar: true,
            showAntrian: false,
            active: "_active",
            isShowing: false

        }
        this._showDaftarDriver = this._showDaftarDriver.bind(this)
        this._showAntrian = this._showAntrian.bind(this)
        cms.getDaftarDriver("Semua");
    }
    _showDaftarDriver() {
        this.setState({
            showDaftar: true,
            showAntrian: false,
            active: "_active"
        });
    }
    _showAntrian() {
        this.setState({
            showAntrian: true,
            showDaftar: false
        });
    }

    openModalHandler = () => {
        this.setState({
            isShowing: true,
            status : "addDriver"
        });
    }

    closeModalHandler = () => {
        this.setState({
            isShowing: false
        });
    }

    render() {

        const renderTables = (_Category = "Tabel Produk") => {
            console.log(this.state.showDaftar)
            console.log(this.state.showAntrian)
            if (this.state.showDaftar === true && this.state.showAntrian === false) {
                return (
                    <Row>
                        <Col>
                        <div className="_background_toggle table-responsive-lg">
                                <button className="_button-toggle _active">Semua</button>
                                <button className="_button-toggle">Aktif</button>
                                <button className="_button-toggle">Libur</button>
                                <button className="_button-toggle">Proses</button>
                                <button className="_button-toggle">Suspend</button>
                                <button className="_button-toggle">Non Aktif</button>
                            </div>
                            <Card small className="mb-4">
                                <CardBody className="p-0 pb-3 table-responsive-lg">
                                    <table className="table mb-0">
                                        <thead className="thead-background-color">
                                            <tr>
                                                <th width="20px" scope="col" className="border-0">
                                                    <input type="checkbox" id="selectall" />
                                                    <label for="selectall"></label>
                                                </th>
                                                <th width="20px" scope="col" className="border-0">Id</th>
                                                <th scope="col" className="border-0">Nama</th>
                                                <th scope="col" className="border-0">Telepon</th>
                                                <th scope="col" className="border-0">Tipe Driver</th>
                                                <th scope="col" className="border-0">Alamat</th>
                                                <th width="20px" scope="col" className="border-0">Status</th>
                                                <th width="30px" scope="col" className="border-0"></th>

                                            </tr>
                                        </thead>
                                        <tbody>
                                            {renderTableItem(products[0])}
                                            {renderTableItem(products[1])}
                                            {renderTableItem(products[2])}
                                            {renderTableItem(products[3])}
                                            {renderTableItem(products[4])}
                                            {renderTableItem(products[5])}
                                        </tbody>
                                    </table>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                );
            } else if (this.state.showDaftar === false && this.state.showAntrian === true) {
                return (
                    <Row>
                    <Col>
                    <div className="_background_toggle table-responsive-lg">
                            <button className="_button-toggle _active">Mobil</button>
                            <button className="_button-toggle">Perempuan</button>
                            <button className="_button-toggle">Motor</button>
                        </div>
                        <Card small className="mb-4">
                            <CardBody className="p-0 pb-3 table-responsive-lg">
                                <table className="table mb-0">
                                    <thead className="thead-background-color">
                                        <tr>
                                            <th width="20px" scope="col" className="border-0">
                                                <input type="checkbox" id="selectall" />
                                                <label for="selectall"></label>
                                            </th>
                                            <th width="20px" scope="col" className="border-0">Id</th>
                                            <th scope="col" className="border-0">Nama</th>
                                            <th scope="col" className="border-0">Telepon</th>
                                            <th scope="col" className="border-0">Alamat</th>

                                        </tr>
                                    </thead>
                                    <tbody>
                                        {renderTableItem2(products[0])}
                                        {renderTableItem2(products[1])}
                                        {renderTableItem2(products[2])}
                                        {renderTableItem2(products[3])}
                                        {renderTableItem2(products[4])}
                                        {renderTableItem2(products[5])}
                                    </tbody>
                                </table>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                )
            }
        }

        return (
            <Container fluid className="main-content-container px-4">
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Manager Driver" subtitle="" className="text-sm-left" />
                    <div className="col">
                        <button className="button-addDriver"
                         onClick={this.openModalHandler}> <i className="large material-icons">add</i>Tambah Driver</button>
                    <Modal
                        className="_modal"
                        show={this.state.isShowing}
                        close={this.closeModalHandler}
                        status={this.state.status}>
                    </Modal>
                    </div>
                </Row>
                <div>
                    <button type="button" onClick={this._showAntrian}
                    className={"_button-toggle2" + (this.state.showAntrian === true? " _Bactive" : "")}>
                        Antrian</button>
                    <button type="button" onClick={this._showDaftarDriver}
                     className={"_button-toggle2" + (this.state.showDaftar === true? " _Bactive" : "")}>Daftar Driver</button>
                </div>
                {renderTables()}
            </Container>
        )
    }
}



export default DriverManagements;
