import React from "react";
import { Container, Row, Col, Card, CardHeader, CardBody, Button } from "shards-react";
import {
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    FormSelect
  } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import SlideToggle from "react-slide-toggle";

const renderProductOrder = (_products, setCollapsibleElement) =>{
    return (
        <tr ref={setCollapsibleElement}>
            <td></td>
            <td colSpan="5">
            <table className="table mb-0 bg-light">
                    <thead className="bg-light">
                        <tr>
                        <th scope="col" className="border-0">
                            No
                        </th>
                        <th scope="col" className="border-0">
                            Id Produk
                        </th>
                        <th scope="col" className="border-0">
                            Nama Produk
                        </th>
                        <th scope="col" className="border-0">
                            Jumlah
                        </th>
                        <th scope="col" className="border-0">
                            Harga
                        </th>
                        </tr>
                    </thead>
                    <tbody>
                        {_products.map((_product,_index)=>{
                            return(
                                <tr>
                                    <td>{_index+1}</td>
                                    <td>{_product.id}</td>
                                    <td>{products.find(product=>product.id===_product.id).nama}</td>
                                    <td>{_product.jumlah}</td>
                                    <td>{_product.jumlah} x {products.find(product=>product.id===_product.id).harga} </td>
                                </tr>
                            );
                        })}
                    </tbody>
            </table>
            </td>
        </tr>
    )
} 

const renderTableItem = (_Item, onToggle) => {
    return(
        <tr>
            <td>{_Item.id}</td>
            <td>{_Item.nama}</td>
            <td>{_Item.alamat}</td>
            <td><Row>
                <Col lg="12">
                    <Button theme="primary" className="mb-1 mr-1" onClick={onToggle}>
                        Lihat Produk 
                    </Button>
                </Col>
                </Row>
            </td>
            <td>{_Item.totalHarga}</td>
            <td>
                <Row>
                <Col lg="12">
                    <Button theme="info" className="mb-1 mr-1">
                        Lacak Pesanan
                    </Button>
                </Col>
                </Row>
            </td>
        </tr>
    );
}

const renderFilter = () =>{
    return(
        <Row>
            <Col>
                <InputGroup className="mb-3">
                    <InputGroupAddon type="prepend">
                        <InputGroupText>Category</InputGroupText>
                    </InputGroupAddon>
                    <FormSelect>
                        <option>Semua Kategori</option>
                        <option>Snack</option>
                        <option>Minuman</option>
                        <option>Lainnya</option>
                    </FormSelect>
                </InputGroup>
            </Col>
        </Row>
    );
}

