const renderTables = (_Category="Tabel Pesanan") =>{
    var tableRender = [];
    return(
        <Row>
            <Col>
                <Card small className="mb-4">
                <CardHeader className="border-bottom">
                    <h6 className="m-0">{_Category}</h6>
                </CardHeader>
                <CardBody className="p-0 pb-3">
                    <table className="table mb-0">
                    <thead className="bg-light">
                        <tr>
                        <th scope="col" className="border-0">
                            ID Pesanan
                        </th>
                        <th scope="col" className="border-0">
                            Nama Pemesan
                        </th>
                        <th scope="col" className="border-0">
                            Alamat Pemesan
                        </th>
                        <th scope="col" className="border-0">
                            Produk
                        </th>
                        <th scope="col" className="border-0">
                            Total Harga
                        </th>
                        <th scope="col" className="border-0">
                            Proses Pesanan
                        </th>
                        </tr>
                    </thead>
                    <tbody>
                        {orderData.map((_data,_index)=>{
                            return(
                            <SlideToggle 
                                duration={0}
                                collapsed = {true}
                                render={({onToggle, setCollapsibleElement, progress})=>(
                                    <div hidden>
                                        {tableRender.push(renderTableItem(_data,onToggle))}
                                        {tableRender.push(renderProductOrder(_data.produk,setCollapsibleElement))}
                                    </div>    
                                )}/>
                            )
                        })}
                        {tableRender}
                    </tbody>
                    </table>
                </CardBody>
                </Card>
            </Col>
        </Row>
    );
}

