import React from "react";
import { Container, Row, Col, Card, CardHeader, CardBody, Button, FormCheckbox } from "shards-react";

import PageTitle from "../components/common/PageTitle";


const renderTableItem = (_Item) => {
    return(
    <tr>
        <td>{_Item.id}</td>
        <td>{_Item.nama}</td>
        <td>{_Item.kategori}</td>
        <td>{_Item.stok}</td>
        <td>{_Item.harga}</td>
        <td>
            <Row>
            <Col lg="6">
                <FormCheckbox toggle small defaultChecked>
                    Produk Aktif
                </FormCheckbox>
            </Col>
            <Col lg="5">
                <Button theme="primary" className="mb-1 mr-1">
                    Detail Produk 
                </Button>
            </Col>
            </Row>
        </td>
    </tr>);
}


const renderTables = (_Category="Tabel Pesanan") =>{
    return(
        <Row>
            <Col>
                <Card small className="mb-4">
                <CardHeader className="border-bottom">
                    <h6 className="m-0">{_Category}</h6>
                </CardHeader>
                <CardBody className="p-0 pb-3">
                    <table className="table mb-0">
                    <thead className="bg-light">
                        <tr>
                        <th scope="col" className="border-0">
                            ID Pesanan
                        </th>
                        <th scope="col" className="border-0">
                            Nama Pemesan
                        </th>
                        <th scope="col" className="border-0">
                            Alamat Pemesan
                        </th>
                        <th scope="col" className="border-0">
                            Produk
                        </th>
                        <th scope="col" className="border-0">
                            Total Harga
                        </th>
                        <th scope="col" className="border-0">
                            Proses Pesanan
                        </th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTableItem(products[0])}
                        {renderTableItem(products[1])}
                        {renderTableItem(products[2])}
                        {renderTableItem(products[3])}
                    </tbody>
                    </table>
                </CardBody>
                </Card>
            </Col>
        </Row>
    );
}

const products = [{
    id: 1,
    nama : "Chitato",
    kategori : "Snack",
    stok : 1000,
    harga : "Rp 11.000,-",
    aktif : true
},{
    id: 2,
    nama : "Indomie",
    kategori : "Snack",
    stok : 2500,
    harga : "Rp 5.000,-",
    aktif : true
},{
    id: 3,
    nama : "Gulagku",
    kategori : "Snack",
    stok : 10,
    harga : "Rp 10.000,-",
    aktif : true
},{
    id: 4,
    nama : "Gulamu",
    kategori : "Snack",
    stok : 6500,
    harga : "Rp 51.000,-",
    aktif : true
}]

class AcceptedOrder extends React.Component{

    // constructor(props){
    //     super(props);
    // }

    render(){
        return(
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Pesanan Baru" subtitle="Terima atau tolak pesanan" className="text-sm-left" />
                </Row>
                {/* Default Light Table */}
                {renderTables()}
            </Container>
        )
    }
}

export default AcceptedOrder;
