class FinishedOrder extends React.Component {

    // constructor(props) {
    //     super(props);
    // }

    render() {
        return (
            <Container fluid className="main-content-container px-4">
                {/* Page Header */}
                <Row noGutters className="page-header py-4">
                    <PageTitle sm="4" title="Pesanan Selesai" subtitle="" className="text-sm-left" />
                </Row>

                {/* Default Light Table */}
                {renderTables()}
            </Container>

        )
    }
}



